import { types } from "../types/types";

export const mainReducer = (state, action) => {
  switch (action.type) {
    case types.cargarEmisores:
      return {
        ...state,
        emisores: [...action.payload],
      };
    case types.loadMkt:
      return {
        ...state,
        mikroTiks: [...action.payload],
      };
    case types.loadUsers:
      return {
        ...state,
        usuarios: [...action.payload],
      };
    case types.loadPings:
      return {
        ...state,
        pings: [...action.payload],
      };
   
    case types.loadOnline:
      return {
        ...state,
        online: action.payload,
      };
    case types.loadOffline:
      return {
        ...state,
        offline: action.payload,
      };
      // ?activar emisor para su datos
      case types.loadEmit:
        return {
          ...state,
          emisorActivo: action.payload,
        };
    case types.cerrarSesion:
      return {
        emisores: [],
        emisorAcitivo:{},
        mikroTiks: [],
        usuarios: [],
        frecuencia: 0,
        widthChannel: 0,
        lan: 0,
        online: 0,
        offline: 0,
      };

    default:
      return state;
  }
};
