import React, { useContext, useEffect } from "react";
import { createContext } from "react";
import { AuthContext } from "../auth/AuthContext";
import { useSocket } from "../hooks/useSocket";
// import resp from "../sound/notificacion.mp3";
// import sendm from "../sound/send.mp3";
import { types } from "../types/types";
import { MainContext } from "./MainContext";

export const SocketContext = createContext();

export const SocketProvider = ({ children }) => {
  // const [audio] = useState(new Audio(resp));
  // const [audiose] = useState(new Audio(sendm));

  const { socket, online, conectarSocket, desconectarSocket } = useSocket(
    // "http://localhost:9004"
    "http://192.168.2.115:9008"
  );

  const { auth } = useContext(AuthContext);
  const { dispatch } = useContext(MainContext);

  useEffect(() => {
    if (auth.logged) {
      conectarSocket();
    }
  }, [auth, conectarSocket]);

  useEffect(() => {
    if (!auth.logged) {
      desconectarSocket();
    }
  }, [auth, desconectarSocket]);

  useEffect(() => {
    socket?.on("listEmit", async (emits) => {
      dispatch({
        type: types.cargarEmisores,

        payload: emits,
      });
    });
  }, [socket, dispatch]);
  useEffect(() => {
    socket?.on("listMkts", async (mkts) => {
      dispatch({
        type: types.loadMkt,
        payload: mkts,
      });
    });
  }, [socket, dispatch]);
  useEffect(() => {
    socket?.on("online", async ({ online, offline }) => {
      dispatch({
        type: types.loadOffline,

        payload: offline,
      });
      dispatch({
        type: types.loadOnline,

        payload: online,
      });
    });
  }, [socket, dispatch]);
  useEffect(() => {
    socket?.on("listUsers", async (us) => {
      dispatch({
        type: types.loadUsers,

        payload: us,
      });
    });
  }, [socket, dispatch]);

  return (
    <SocketContext.Provider value={{ socket, online }}>
      {children}
    </SocketContext.Provider>
  );
};
