import React, { useEffect } from "react";
import { ToastContainer, toast } from "react-toastify";

import { useContext } from "react";
import { useState } from "react";
import { Button, Modal, Form } from "react-bootstrap";
import { SocketContext } from "../context/SocketContext";
import { useForm } from "../hooks/useForm";

export const EdItEmit = (props) => {
  const { socket } = useContext(SocketContext);

  const { emisor, radio } = props;
  const {
    id = "",
    nombre,
    ip,
    puerto,
    signal,
    marca,
    modelo,
    comunidad,
    version,
    modo,
    tx,
    rx,
  } = emisor;
  const [check, setCheck] = useState(radio);
  const [values, handleInputChange, setFormValue] = useForm({
    nombre: "",
    ip: "",
    puerto: "",
    signal: "",
    marca: "Mimosa",
    modelo: "",
    comunidad: "",
    version: "V1",
    modo: "AP",
    tx: "",
    rx: "",
  });
  useEffect(() => {
    setForm();
  }, [id]);
  const setForm = () => {
    if (id.length === 0) return;
    setFormValue({
      nombre,
      ip,
      puerto,
      signal,
      marca,
      modelo,
      comunidad,
      version,
      modo,
      tx,
      rx,
    });
  };
  const onSubmit = (e) => {
    e.preventDefault();
    const data = {
      nombre: values.nombre,
      ip: values.ip,
      puerto: values.puerto,
      signal: values.signal,
      marca: values.marca,
      modelo: values.modelo,
      monitoreo: check,
      comunidad: values.comunidad,
      version: values.version,
      modo: values.modo,
      tx: values.tx,
      rx: values.rx,
    };



    // toast.error('Error, verifica que tus datos sean correctos!', {
    //   position: "top-center",
    //   autoClose: 4000,
    //   hideProgressBar: false,
    //   closeOnClick: true,
    //   pauseOnHover: true,
    //   draggable: true,
    //   progress: undefined,
    //   });

    if (id.length > 0) {
      socket.emit("editEmisor", { id, ...data });
    } else {
      socket.emit("addEmisor", data);
    }
    props.handleClose();
    // reset();
  };
  return (
    // <div> yuu</div>
    <Modal show={props.show} onHide={props.handleClose} animation={false}>
      <Modal.Header closeButton className="bg-light">
        {id.length > 0 ? <h5>Editar Emisor</h5> : <h5>Nuevo Emisor</h5>}
      </Modal.Header>
      <Modal.Body>
        <Form>
          <div className="mb-3 row">
            <label className="col-sm-2 col-form-label">Nombre</label>
            <div className="col-sm-10">
              <Form.Control
                id="area"
                name="nombre"
                value={values.nombre}
                onChange={handleInputChange}
                placeholder="Emisor"
              ></Form.Control>
            </div>
          </div>
          <div className="mb-3 row">
            <label className="col-sm-2 col-form-label">Ip</label>
            <div className="col-sm-10">
              <Form.Control
                name="ip"
                value={values.ip}
                onChange={handleInputChange}
                placeholder="ip"
              ></Form.Control>
            </div>
          </div>

          <div className="mb-3 row">
            <label className="col-sm-2 col-form-label">Puerto</label>
            <div className="col-sm-4">
              <Form.Control
                name="puerto"
                value={values.puerto}
                onChange={handleInputChange}
                placeholder="puerto"
              ></Form.Control>
            </div>

            <label className="col-sm-2 col-form-label">Seal</label>
            <div className="col-sm-4">
              <Form.Control
                name="signal"
                value={values.signal}
                onChange={handleInputChange}
                placeholder="Seal"
              ></Form.Control>
            </div>
          </div>

          <div className="mb-3 row">
            <label className="col-sm-2 col-form-label">Tx</label>
            <div className="col-sm-4">
              <Form.Control
                name="tx"
                value={values.tx}
                onChange={handleInputChange}
                placeholder="TX"
              ></Form.Control>
            </div>

            <label className="col-sm-2 col-form-label">Rx</label>
            <div className="col-sm-4">
              <Form.Control
                name="rx"
                value={values.rx}
                onChange={handleInputChange}
                placeholder="RX"
              ></Form.Control>
            </div>
          </div>
          <div className="mb-3 row">
            <label className="col-sm-2 col-form-label">Marca</label>

            <div className="col-sm-10">
              <Form.Select
                value={values.marca}
                onChange={handleInputChange}
                name="marca"
              >
                <option>Mimosa</option>
                <option>Ubiquiti</option>
                <option>Ubiquiti AC</option>
              </Form.Select>
            </div>
          </div>
          <div className="mb-3 row">
            <label className="col-sm-2 col-form-label">Modelo</label>
            <div className="col-sm-10">
              <Form.Control
                id="area"
                name="modelo"
                value={values.modelo}
                onChange={handleInputChange}
                placeholder="Modelo"
              ></Form.Control>
            </div>
          </div>

          <div className="mb-3 row">
            <label className="col-sm-2 col-form-label">Modo</label>
            <div className="col-sm-10">
              <Form.Select
                name="modo"
                value={values.modo}
                onChange={handleInputChange}
              >
                <option>AP</option>
                <option>PTP</option>
              </Form.Select>
            </div>
          </div>
          <div className="mb-3 row">
            <label className="col-sm-2 col-form-label">Monitoreo</label>
            <div className="col-sm-4">
              <Form.Check
                className="ms-4 mt-2 "
                type="switch"
                id="custom-switch3"
                name="snmp"
                checked={check}
                onChange={() => {
                  setCheck(!check);
                }}
              />
            </div>
            <label className="col-sm-2 col-form-label">Version</label>
            <div className="col-sm-4">
              <Form.Select
                value={values.version}
                name="version"
                onChange={handleInputChange}
              >
                <option>V1</option>
                <option>V2</option>
                <option>V3</option>
              </Form.Select>
            </div>
          </div>
          <div className="mb-3 row">
            <label className="col-sm-3 col-form-label">Comunidad</label>
            <div className="col-sm-9">
              <Form.Control
                id="area"
                name="comunidad"
                value={values.comunidad}
                onChange={handleInputChange}
                placeholder="Comunidad"
              ></Form.Control>
            </div>
          </div> 
      
        </Form>
           {(values.modo==='PTP' && values.marca!=='Mimosa') && (
         <div className="alert alert-warning text-xl-start p-1 m-1 " role="alert">
         Asegurate de agregar los oids de las capacidades antes de activar el monitoreo 
       </div>
        )}
        {(check) && ( 
         <div className="alert alert-danger text-xl-start p-1 m-1" role="alert">
         *Asegurate de que el protocolo SNMP este activo en tu equipo
       </div>
        )}
      </Modal.Body>
      <Modal.Footer>
        <Button variant="secondary" onClick={props.handleClose}>
          Calcelar
        </Button>
        <Button variant="primary" onClick={onSubmit}>
          Guardar
        </Button>
      </Modal.Footer>
      <ToastContainer />
    </Modal>
  );
};
