import React, { useContext } from "react";
import { RiWifiFill, RiWifiOffFill } from "react-icons/ri";
import { MainContext } from "../context/MainContext";

import "../css/cards.css";
export const Cards = () => {
  const { mainState } = useContext(MainContext);
  const { online, offline } = mainState;

  return (
    <div className="row mx-2">
      <div className="col-sm-4">
        <div className="card card-succ shadow  bg-body rounded">
          <div className="card-body row">
            <div className="col-4 text-center">
              <RiWifiFill className="icono2" />
            </div>
            <div className="col-8 text-center">
              <h6 className="card-title ">Equipos Online</h6>
              <b className="fs-3">{online}</b>
            </div>
          </div>
        </div>
      </div>
      <div className="col-sm-4">
        <div className="card card-warn shadow  bg-body rounded">
          <div className="card-body row">
            <div className="col-4 text-center">
              <RiWifiOffFill className="icono" />
            </div>
            <div className="col-8 text-center">
              <h6 className="card-title ">Equipos Offline</h6>
              <b className="fs-3">{offline}</b>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
