import React, { createContext, useCallback, useState } from "react";

import { fetchConToken, fetchSinToken } from "../helpers/fetch";

export const AuthContext = createContext();

const initialState = {
  uid: null,
  checking: true,
  logged: false,
  name: null,
  email: null,
  foto: null,
  permisos: null,
  rol: null,
};

export const AuthProvider = ({ children }) => {
  const [auth, setAuth] = useState(initialState);
  const login = async (email, password) => {
    const resp = await fetchSinToken("auth/login", { email, password }, "POST");
    if (resp.token) {
      localStorage.setItem("token", resp.token);

      const { usuario, rol } = resp;
      // console.log(permisos,modulos);
      setAuth({
        uid: usuario.id,
        checking: false,
        logged: true,
        name: usuario.nombre,
        email: usuario.email,
        roll: rol,
        foto: usuario.foto,
      });
    }
  };

  const register = async (nombre, email, password, roll, data) => {
    // console.log(nombre, email, password,selectroll,data);
    const resp = await fetchConToken(
      "login/new",
      { nombre, email, password, roll, data },
      "POST"
    );

    if (resp.ok) {
      return true;
    }

    return resp.msg;
  };

  const verificaToken = useCallback(async () => {
    const token = localStorage.getItem("token");
    // Si token no existe
    if (!token) {
      setAuth({
        uid: null,
        checking: false,
        logged: false,
        name: null,
        email: null,
        roll: null,
        img: null,
      });

      return false;
    }

    const resp = await fetchConToken("auth");
    if (resp.token) {
      localStorage.setItem("token", resp.token);

      const { usuario } = resp;

      setAuth({
        uid: usuario.id,
        checking: false,
        logged: true,
        name: usuario.nombre,
        email: usuario.email,
        roll: usuario.rol,
        foto: usuario.foto,
      });

      return true;
    } else {
      setAuth({
        id: null,
        checking: false,
        logged: false,
        name: null,
        email: null,
        foto: null,
        roll: null,
      });

      return false;
    }
  }, []);

  const logout = () => {
    localStorage.removeItem("token");
    setAuth({
      checking: false,
      logged: false,
    });
  };

  return (
    <AuthContext.Provider
      value={{
        auth,
        login,
        register,
        verificaToken,
        logout,
      }}
    >
      {children}
    </AuthContext.Provider>
  );
};
