import React from "react";
import { GiLightningFrequency } from "react-icons/gi";
import { AiOutlineColumnWidth } from "react-icons/ai";
import { FaEthernet } from "react-icons/fa";

import "../../css/cards.css";
export const CardsEmit = ({ frecuencia, lan, widthChannel }) => {
  return (
    <div className="row mx-2">
      <div className="col">
        <div className="card card-succ shadow  bg-body rounded">
          <div className="card-body row">
            <div className="col-4 text-center">
              <GiLightningFrequency className="icono2" />
            </div>
            <div className="col-8 text-center">
              <h6 className="card-title ">Frecuencia</h6>
              <b className="fs-3">{frecuencia} MHz</b>
            </div>
          </div>
        </div>
      </div>
      <div className="col">
        <div className="card card-warn shadow  bg-body rounded">
          <div className="card-body row">
            <div className="col-4 text-center">
              <AiOutlineColumnWidth className="icono" />
            </div>
            <div className="col-8 text-center">
              <h6 className="card-title ">Ancho de canal</h6>
              <b className="fs-3">{widthChannel} MHz</b>
            </div>
          </div>
        </div>
      </div>
      <div className="col">
        <div className="card card-succ shadow  bg-body rounded">
          <div className="card-body row">
            <div className="col-4 text-center">
              <FaEthernet className="icono2" />
            </div>
            <div className="col-8 text-center">
              <h6 className="card-title ">LAN</h6>
              <b className="fs-3">{lan} Mbps</b>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
