import React, { useContext } from "react";
import { Modal, Container, Form, Button } from "react-bootstrap";
import { AuthContext } from "../auth/AuthContext";
import { SocketContext } from "../context/SocketContext";

export const ConfirmDelete = (props) => {
  const { item } = props;
  const { socket } = useContext(SocketContext);
  const { auth } = useContext(AuthContext);

  const onSubmit = () => {
    if (item.type === "emisor") {
      socket.emit("delEmisor", item.id);
    }
    if (item.type === "usuario") {
      socket.emit("delUser", { id: item.id, id_user: auth.uid });
    }
    if (item.type === "MikroTik") {
      socket.emit("delMkt", item.id);
    }
    props.handleClose();
  };
  return (
    <Modal
      size="sm"
      show={props.show}
      onHide={props.handleClose}
      aria-labelledby="example-modal-sizes-title-sm"
    >
      <Modal.Header closeButton>
        <Modal.Title id="example-modal-sizes-title-sm">Eliminar</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        Esta seguro de que desea eliminar el {item.type} - {item.nombre}
      </Modal.Body>
      <Modal.Footer>
        <Button
          onClick={props.handleClose}
          className="btn btn-light border border-gray btn-sm"
        >
          Cancelar
        </Button>
        <Button onClick={onSubmit} className="btn btn-sm">
          Eliminar
        </Button>
      </Modal.Footer>
    </Modal>
  );
};
