import React from "react";
import { Cards } from "../components/Cards";
import { List } from "../components/List";
import { ListMkt } from "../components/ListMkt";

export const Dasboard = () => {
  return (
    <div
      className="overflow-auto"
      style={{
        height: "100vh",
        width: "84%",
        justify: "center",
        background: "#ecedef",
      }}
    >
      <div style={{ background: "#04c294" }} className="col-12">
        <h2 className="bold text-white  p-2"> Dashboard </h2>
      </div>
      <div className="row">
        <div className="justify-content-center">
          <Cards className="col-auto" />
        </div>
      </div>
      <List />
      <ListMkt />
    </div>
  );
};
