import React, { useContext, useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { Capacity } from "../components/Graphics/Capacity";
import { CardsEmit } from "../components/Graphics/CardsEmit";
import { IntencitySignal } from "../components/Graphics/IntencitySignal";
import { Noise } from "../components/Graphics/Noise";
import { Ping } from "../components/Graphics/Ping";
import { MainContext } from "../context/MainContext";
import { fetchConToken } from "../helpers/fetch";
import { types } from "../types/types";
export const Graficas = () => {
  const { mainState, dispatch } = useContext(MainContext);
  const { emisorActivo } = mainState;
  const { id } = useParams();
  const [loading, setLoading] = useState(true);
  const { frecc = 0, wichanel = 0, lan = 0, modo, marca,nombre } = emisorActivo;
  useEffect(() => {
    fetchAux();
  }, []);
  const fetchAux = async () => {
    const resp = await fetchConToken(`roteos/emisor`, { id }, "POST");
    if (resp.Emit) {
      dispatch({
        type: types.loadEmit,
        payload: resp.Emit,
      });
      setLoading(false);
    }
  };
  if (loading) {
    return (
      <div className="col-12 card bg-main justify-content-center">
        <div className="text-center col-auto">
          <div className="spinner-border text-success">
            <span className="visually-hidden">Loading...</span>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div
      className="overflow-auto"
      style={{
        height: "100vh",
        width: "84%",
        justify: "center",
        background: "rgb(245, 245, 245)",
      }}
    >
      <div style={{ background: "#04c294" }} className="col-12 mb-3">
        <h2 className="bold text-white  p-2">Estadisticas {nombre}</h2>
      </div>

      <div className="row">
        <div className="col-sm-12 mt-4 mb-2">
          <CardsEmit frecuencia={frecc} widthChannel={wichanel} lan={lan} />
        </div>
      </div>

      <div id="chart" className="card">
        <div className="row">
          <Noise id={id} />
          {marca === "Mimosa" && <Capacity id={id} />}
          {marca === "Ubiquiti" && modo === "PTP" && <Capacity id={id} />}
          {marca === "Ubiquiti AC" && modo === "PTP" && <Capacity id={id} />}

          {marca === "Mimosa" && <IntencitySignal id={id} />}
          {marca === "Ubiquiti" && modo === "PTP" && (
            <IntencitySignal id={id} />
          )}
          {marca === "Ubiquiti AC" && modo === "PTP" && (
            <IntencitySignal id={id} />
          )}

          <Ping id={id} />
        </div>
      </div>
    </div>
  );
};
