import React, { useContext, useEffect } from "react";
import { Button, Modal, Form } from "react-bootstrap";
import { SocketContext } from "../context/SocketContext";
import { useForm } from "../hooks/useForm";

export const OperOids = (props) => {
  const { socket } = useContext(SocketContext);

  const { id } = props;

  const [values, handleInputChange,  setFormValue] = useForm({
    oid: "",
    oid2: "",
    id_oids: "",
  });
  useEffect(() => {
    getOids();
  }, [id]);
  const getOids = async () => {
    if (id === 0) return;
    const resp = await fetch(
      `http://192.168.2.115:9008/api/roteos/${id}`,
      {
        method: "GET",
        //  headers: headersList
      }
    );
    if (resp.status === 200) {
      const ressult = await resp.json();
      const { oid, oid2, id } = ressult.data;
      setFormValue({ oid, oid2, id_oids: id });
    }
  };
 
  const onSubmit = (e) => {
    e.preventDefault();
    if (values.oid.length === 0 && values.oid2.length === 0) return;
    const data = {
      oid: values.oid,
      oid2: values.oid2,
      id_roteos: id,
    };
    console.log(data);
    if (values.id_oids.length > 0) {
      socket.emit("editOids", data);
    } else {
      socket.emit("addOids", data);
    }
    // reset();
    props.handleClose();
  };

  return (
    <Modal show={props.show} onHide={props.handleClose} animation={false}>
      <Modal.Header closeButton className="bg-light">
        <h5> Oids</h5>
      </Modal.Header>
      <Modal.Body>
        <Form>
          <div className="mb-3 row">
            <label className="col-sm-2 col-form-label">Oid 1</label>
            <div className="col-sm-10">
              <Form.Control
                name="oid"
                value={values.oid}
                onChange={handleInputChange}
                placeholder="1.3.6.1.4.1.41112.1.4.7.1.17.1..."
              ></Form.Control>
            </div>
          </div>
          <div className="mb-3 row">
            <label className="col-sm-2 col-form-label">Oid 2</label>
            <div className="col-sm-10">
              <Form.Control
                name="oid2"
                value={values.oid2}
                onChange={handleInputChange}
                placeholder="1.3.6.1.4.1.41112.1.4.7.1.18.1..."
              ></Form.Control>
            </div>
          </div>
        </Form>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="secondary" onClick={props.handleClose}>
          Calcelar
        </Button>
        <Button variant="primary" onClick={onSubmit}>
          Guardar
        </Button>
      </Modal.Footer>
    </Modal>
  );
};
