import React, { useEffect } from "react";
import { useContext } from "react";
import { Button, Modal, Form } from "react-bootstrap";
import { SocketContext } from "../../context/SocketContext";
import { useForm } from "../../hooks/useForm";

export const OperMkts = (props) => {
  const { socket } = useContext(SocketContext);

  const { mktik } = props;
  const { id = "", nombre, ip, puerto } = mktik;

  const [values, handleInputChange, reset, setFormValue] = useForm({
    nombre: "",
    ip: "",
    puerto: "",
  });
  useEffect(() => {
    setForm();
  }, [id]);
  const setForm = () => {
    if (id.length === 0) return;
    setFormValue({
      nombre,
      ip,
      puerto,
    });
  };
  const onSubmit = (e) => {
    e.preventDefault();
    const data = {
      nombre: values.nombre,
      ip: values.ip,
      puerto: values.puerto,
    };
    if (id.length > 0) {
      socket.emit("editMkt", { id, ...data });
    } else {
      socket.emit("addMkt", data);
    }
    props.handleClose();
    reset();
  };
  return (
    // <div> yuu</div>
    <Modal show={props.show} onHide={props.handleClose} animation={false}>
      <Modal.Header closeButton className="bg-light">
        {id.length > 0 ? <h5>Editar MikroTik</h5> : <h5>Nuevo MikroTik</h5>}
      </Modal.Header>
      <Modal.Body>
        <Form>
          <div className="mb-3 row">
            <label className="col-sm-2 col-form-label">Nombre</label>
            <div className="col-sm-10">
              <Form.Control
                id="area"
                name="nombre"
                value={values.nombre}
                onChange={handleInputChange}
                placeholder="Emisor"
              ></Form.Control>
            </div>
          </div>
          <div className="mb-3 row">
            <label className="col-sm-2 col-form-label">Ip</label>
            <div className="col-sm-10">
              <Form.Control
                name="ip"
                value={values.ip}
                onChange={handleInputChange}
                placeholder="ip"
              ></Form.Control>
            </div>
          </div>

          <div className="mb-3 row">
            <label className="col-sm-2 col-form-label">Puerto</label>
            <div className="col-sm-4">
              <Form.Control
                name="puerto"
                value={values.puerto}
                onChange={handleInputChange}
                placeholder="puerto"
              ></Form.Control>
            </div>
          </div>
        </Form>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="secondary" onClick={props.handleClose}>
          Calcelar
        </Button>
        <Button variant="primary" onClick={onSubmit}>
          Guardar
        </Button>
      </Modal.Footer>
    </Modal>
  );
};
