import React, { useState } from "react";
import { useContext } from "react";
import { MainContext } from "../context/MainContext";
// import { Addmktt } from "./Addmktt";
import { SiGoogleearth } from "react-icons/si";
import { FaPenSquare, FaRegTrashAlt } from "react-icons/fa";

import { ConfirmDelete } from "./ConfirmDelete";
import { NavLink } from "react-router-dom";
import { OperMkts } from "./Mikrotiks/OperMkts";
import { AddMkt } from "./Mikrotiks/AddMkt";
export const ListMkt = () => {
  const { mainState } = useContext(MainContext);
  const { mikroTiks } = mainState;
  const [show, setShow] = useState(false);

  const [mktt, setmktt] = useState({});
  const [smShow, setSmShow] = useState(false);
  const [dEdit, setdEdmit] = useState({});
  const editmktt = (mktt) => {
    setmktt(mktt);
    setShow(true);
  };
  const delShow = (item) => {
    setdEdmit({ id: item.id, nombre: item.nombre, type: "MikroTik" });
    setSmShow(true);
  };

  return (
    <div className="card m-3">
      <div className="card-header bg-info bold">MikroTiks</div>
      <nav className="navbar navbar-light bg-light">
        <div className="container-fluid">
          <AddMkt />
        </div>
      </nav>
      {mikroTiks.length > 0 && (
        <div className="card-body shadow  bg-body rounded">
          <div className="table-responsive">
            <table className="table   table-striped p-0">
              <thead>
                <tr>
                  <th scope="col">Nombre</th>
                  <th scope="col">Equipo</th>
                  <th scope="col">IP</th>

                  <th scope="col">Estado</th>

                  <th scope="col"></th>
                </tr>
              </thead>
              <tbody>
                {mikroTiks.map((mkt) => (
                  <tr key={mkt.id} className="p-0">
                    <td className="p-0">
                      <NavLink
                        className="nav-item nav-link"
                        to={`/graficos/${mkt.id}/false`}
                      >
                        {mkt.nombre}
                      </NavLink>
                    </td>

                    <td className="p-0">
                      <SiGoogleearth className="bold me-1" />

                      {mkt.marca}
                    </td>
                    <td className="p-0">
                      {mkt.puerto ? (
                        <a
                          className="nav-link"
                          href={"http://" + mkt.ip + ":" + mkt.puerto}
                          target="_blank"
                          rel="noopener noreferrer"
                        >
                          {mkt.ip + ":" + mkt.puerto}
                        </a>
                      ) : (
                        <a
                          className="nav-link"
                          href={"http://" + mkt.ip}
                          target="_blank"
                          rel="noopener noreferrer"
                        >
                          {mkt.ip}
                        </a>
                      )}
                    </td>
                    <td className="p-0">
                      {mkt.online === 0 ? (
                        <div className=" alert-danger text-center">Offline</div>
                      ) : (
                        <div className=" alert-success text-center">Online</div>
                      )}
                    </td>
                    <td className="text-center p-0">
                      <FaPenSquare
                        style={{
                          color: "#0298db",
                          fontSize: "23px",
                          marginRight: "10px",
                        }}
                        onClick={() => editmktt(mkt)}
                      />
                      <FaRegTrashAlt
                        style={{ color: "#0298db", fontSize: "20px" }}
                        onClick={() => delShow(mkt)}
                      />
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      )}

      <OperMkts
        show={show}
        handleClose={() => {
          setShow(false);
        }}
        mktik={mktt}
      />
      <ConfirmDelete
        show={smShow}
        handleClose={() => {
          setSmShow(false);
        }}
        item={dEdit}
      />
    </div>
  );
};
