export const getWallet = async (endpoint) => {
  const url = `${baseUrl}/${endpoint}`;
  const resp = await fetch(url);
  const { data } = await resp.json();
  return data;
};
export const getRol = async (endpoint) => {
  const url = `${baseUrl}/${endpoint}`;
  const resp = await fetch(url);
  const roll = await resp.json();

  return roll;
};

export const subArchivo = async (formData, ruta) => {
  // console.log(datos);
  try {
    const url = `${baseUrl}/${ruta}`;
    const res = await axios.post(url, formData, {
      headers: {
        "Content-Type": "multipart/form-data",
      },
    });
    return res;
  } catch (error) {
    console.log(error);
  }
};
