import React, { useContext, useState } from "react";
import { NavLink } from "react-router-dom";

import {
  ProSidebar,
  Menu,
  MenuItem,
  SidebarHeader,
  SidebarContent,
  SidebarFooter,
} from "react-pro-sidebar";
import "react-pro-sidebar/dist/css/styles.css";
import { FaGem, FaUserAlt } from "react-icons/fa";
import logo from "../img/logo.png";

import "../css/sidebar.css";
import { AuthContext } from "../auth/AuthContext";
import { FaRegUserCircle } from "react-icons/fa";
export const SideBar = () => {
  const { auth, logout } = useContext(AuthContext);
  const { name, foto } = auth;
  const width = window.innerWidth;
  const [togg, setTggle] = useState(false);
  if (width < 600) {
    setTggle(true);
  }

  return (
    <ProSidebar
      image="https://i.pinimg.com/originals/dd/b4/f0/ddb4f078c7dd7410799460380e8ed738.png"
      width="15%"
      collapsed={togg}
    >
      <SidebarHeader>
        <div className="flex text-white justify-content-center align-self-lg-center">
          {foto > 0 ? (
            <img src={foto} alt={name} />
          ) : (
            <FaRegUserCircle className="fs-2 m-3" />
          )}

          <div
            className="flex"
            style={{ flexDirection: "row", display: "inline-block" }}
          >
            <b className="fs-3 mt-2">{name}</b>
            <button
              type="button"
              className="btn text-white dropdown-toggle dropdown-toggle-split"
              data-bs-toggle="dropdown"
              aria-expanded="false"
            >
              <span className="visually-hidden">Toggle Dropdown</span>
            </button>
            <ul className="dropdown-menu bg-light">
              <li className=" pe-auto m-1" onClick={logout}>
                Salir
              </li>
            </ul>
          </div>
        </div>
      </SidebarHeader>
      <SidebarContent>
        <Menu iconShape="square">
          <MenuItem icon={<FaGem className="bg-blue" />}>
            Dashboard <NavLink className="nav-item nav-link" to="/" />
          </MenuItem>

          <MenuItem icon={<FaUserAlt />}>
            Usuarios <NavLink className="nav-item nav-link" to="/usuarios" />
          </MenuItem>

          {/* <SubMenu
            title="Components"
            icon={<FaHeart />}
          >
            <MenuItem>Component 1</MenuItem>
            <MenuItem>Component 2</MenuItem>
          </SubMenu> */}
        </Menu>
      </SidebarContent>
      <SidebarFooter>
        <div className="text-center p3 m-3">
          <img src={logo} alt="rwt" width="50%" />
        </div>
      </SidebarFooter>
    </ProSidebar>
  );
};
