import React, { useContext, useEffect, useState } from 'react';
import rtw from '../img/logo.png';
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { AuthContext } from '../auth/AuthContext';
import { NavLink } from 'react-router-dom';

export const LoginPage = () => {

    const { login } = useContext( AuthContext );
    
    const [ form, setForm ] = useState({
        email: '',
        password: '',
        rememberme: false
    });

    useEffect(() => {
        const email = localStorage.getItem('email');
        if ( email ) {
            setForm( (form) => ({
                ...form,
                email,
                rememberme: true,
            }));
        }

    }, [])


    const onChange = ({ target }) => {
        const { name, value } = target;
        setForm({
            ...form,
            [name]: value
        });
    }

    const toggleCheck = () => {
      
        setForm({
            ...form,
            rememberme: !form.rememberme
        });
    }

    const onSubmit = async(ev) => {
        ev.preventDefault();

        (form.rememberme) 
            ? localStorage.setItem('email', form.email )
            : localStorage.removeItem('email');
        
        const { email, password } = form;    
    
        const ok = await login( email, password );
// console.log(ok);
        if ( !ok ) {
            toast.error('Error, verifica que tus datos sean correctos!', {
                position: "top-center",
                autoClose: 4000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                });
        }
    }

    const todoOk = () => {
        return ( form.email.length > 0 && form.password.length > 0 ) ? true : false;
    }


    return (
        <form 
            className="login100-form validate-form flex-sb flex-w p-5 rounded" 
            onSubmit={ onSubmit }
        >
          
            <img src={rtw} alt="Runtimewifi"  width="85%" className="m-3 align-items-center" />
             
            <div className="wrap-input100 validate-input mb-3">
                <input
                    className="input100"
                    type="email"
                    name="email"
                    placeholder="Email"
                    value={ form.email }
                    onChange={ onChange }
                />
                <span className="focus-input100"></span>
            </div>
            
            
            <div className="wrap-input100 validate-input mb-3">
                <input
                    className="input100"
                    type="password"
                    name="password"
                    placeholder="Password" 
                    value={ form.password }
                    onChange={ onChange }
                />
                <span className="focus-input100"></span>
            </div>
            
            <div className="row mb-3">
                <div 
                    className="col"
                    onClick={ ()=> toggleCheck() }
                >
                    <input
                        className="input-checkbox100"
                        id="ckb1"
                        type="checkbox"
                        name="rememberme" 
                        checked={ form.rememberme }
                        readOnly
                    />
                    <label className="label-checkbox100">
                        Recordarme
                    </label>
                </div>
            </div>
        
            <div className="container-login100-form-btn m-t-17 ">
           
                <button 
                    type="submit"
                    className="login100-form-btn rounded-pill mb-2"
                    disabled={ !todoOk() }
                >
                    Ingresar
                </button>
                
                <NavLink  to="/">
                    Olvide Contrasea
                  </NavLink>
            </div>
            <ToastContainer />

        </form>
    )
}
