
import { useEffect } from "react";
import { useState } from "react";
import { fetchConToken } from "../helpers/fetch";

export const useGraphic = ({ id, time,endpoint, type='',ip='',modo=''}) => {
  const [loading, setLoading] = useState(true);
  const [data, setData] = useState([]);
  const [fechas, setFechas] = useState([]);

  useEffect(() => {
    getEx({ id, time });
  }, []);

  const getEx = async ({ id, time }) => {
    const resp = await fetchConToken(endpoint, { id, time ,type,ip,modo}, "POST");
    if (resp.ok) {
      const { data, fechas } = resp;
      setData(data);
      setFechas(fechas);
    }
    setLoading(false);
  };
  const setValues = ({ id, time }) => {
    getEx({ id, time });
  };

  return { loading, data, fechas, setValues };
};
