import React, { useState } from "react";
import { useContext } from "react";
import { MainContext } from "../context/MainContext";
import { AddEmit } from "./AddEmit";
import { SiUbiquiti, SiChartmogul } from "react-icons/si";
import { FaPenSquare, FaRegTrashAlt, FaGripHorizontal } from "react-icons/fa";
import { EdItEmit } from "./EdItEmit";
import { ConfirmDelete } from "./ConfirmDelete";
import { NavLink } from "react-router-dom";

import { OperOids } from "./OperOids";
import { Paginate } from "./Paginate";
import { types } from "../types/types";
export const List = () => {
  const { mainState,dispatch } = useContext(MainContext);

  const { emisores } = mainState;
  const [show, setShow] = useState(false);
  const [check, setCheck] = useState(true);
  const [emit, setEmit] = useState({});
  const [smShow, setSmShow] = useState(false);
  const [dEdit, setdEdmit] = useState({});


  // ?states para oids
  const [Oid, setOid] = useState(0);
  const [showO, setShO] = useState(false);
  const editOid = (id) => {
    setOid(id);
    setShO(true);
  };
  const editEmit = (emit) => {
    if (emit.monitoreo === 0) {
      setCheck(false);
    }
    setEmit(emit);
    setShow(true);
  };
  const delShow = (item) => {
    setdEdmit({ id: item.id, nombre: item.nombre, type: "emisor" });
    setSmShow(true);
  };
const activeEmit = (emi)=>{
  dispatch({
    type: types.loadEmit,
    payload: emi,
  });
}
  return (
    <div className="card m-3">
      <div className="card-header bg-info bold">Emisores</div>
      <nav className="navbar navbar-light bg-light">
        <div className="container-fluid">
          <AddEmit />
        </div>
      </nav>

      <div className="card-body shadow  bg-body rounded">
        {emisores.length > 0 && (
          <div className="table-responsive">
            <table className="table  table-hover table-striped p-0">
              <thead>
                <tr>
                  <th scope="col">Nombre</th>
                  <th scope="col">Equipo</th>
                  <th scope="col">IP</th>
                  <th scope="col">Frecuencia</th>
                  <th scope="col">Ancho de canal</th>
                  <th scope="col">Estado</th>

                  <th scope="col"></th>
                </tr>
              </thead>
              <tbody>
                {emisores.map((emi) => (
                  <tr key={emi.id} className="p-0">
                    <td className="p-0">
                      <NavLink
                        className="nav-item nav-link"
                        to={`/graficos/${emi.id}/true`}
                        onClick={()=>activeEmit(emi)}
                      >
                        {emi.nombre}
                      </NavLink>
                    </td>

                    <td className="p-0">
                      {(emi.marca === "Ubiquiti" ||
                        emi.marca === "Ubiquiti AC") && (
                        <SiUbiquiti className="text-primary me-1" />
                      )}
                      {emi.marca === "Mimosa" && (
                        <SiChartmogul className="text-warning me-1" />
                      )}

                      {emi.marca}
                    </td>
                    <td className="p-0">
                      {emi.puerto ? (
                        <a
                          className="nav-link"
                          href={"http://" + emi.ip + ":" + emi.puerto}
                          target="_blank"
                          rel="noopener noreferrer"
                        >
                          {emi.ip + ":" + emi.puerto}
                        </a>
                      ) : (
                        <a
                          className="nav-link"
                          href={"http://" + emi.ip}
                          target="_blank"
                          rel="noopener noreferrer"
                        >
                          {emi.ip}
                        </a>
                      )}
                    </td>
                    <td className="p-0">{emi.frecc} MHz</td>
                    <td className="p-0">{emi.wichanel} MHz</td>
                    <td className="p-0">
                      {emi.online === 0 ? (
                        <div className=" alert-danger text-center">Offline</div>
                      ) : (
                        <div className=" alert-success text-center">Online</div>
                      )}
                    </td>
                    <td className="text-center p-0">
                      {emi.marca !== "Mimosa" &&
                        emi.monitoreo === 1 &&
                        emi.modo === "PTP" && (
                          <FaGripHorizontal
                            title="Oids"
                            style={{
                              color: "#0298db",
                              fontSize: "23px",
                              marginRight: "10px",
                            }}
                            onClick={() => editOid(emi.ip)}
                          />
                        )}
                      <FaPenSquare
                        style={{
                          color: "#0298db",
                          fontSize: "23px",
                          marginRight: "10px",
                        }}
                        onClick={() => editEmit(emi)}
                      />
                      <FaRegTrashAlt
                        style={{ color: "#0298db", fontSize: "20px" }}
                        onClick={() => delShow(emi)}
                      />
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        )}
        <Paginate className="m-4" />
      </div>

      {/* show={isOpened2}
        onHide={() => setIsOpened2(false)} */}
      <OperOids
        show={showO}
        handleClose={() => {
          setShO(false);
        }}
        id={Oid}
      />
      <EdItEmit
        show={show}
        handleClose={() => {
          setShow(false);
        }}
        emisor={emit}
        radio={check}
      />
      <ConfirmDelete
        show={smShow}
        handleClose={() => {
          setSmShow(false);
        }}
        item={dEdit}
      />
    </div>
  );
};
