import React, { useContext, useEffect } from "react";
import { Button, Modal, Form } from "react-bootstrap";

import { SocketContext } from "../context/SocketContext";
import { useForm } from "../hooks/useForm";

export const OppUsers = (props) => {
  const { socket } = useContext(SocketContext);

  const { usuario } = props;
  const { id = "", nombre, email, rol } = usuario;
  const [values, handleInputChange, reset, setFormValue] = useForm({
    nombre: "",
    email: "",
    role: "ADMIN_ROLE",
    password: "",
  });
  useEffect(() => {
    setForm();
  }, [id]);
  const setForm = () => {
    if (id.length === 0) {
      setFormValue({
        nombre: "",
        email: "",
        role: "ADMIN_ROLE",
        password: "",
      });
    } else {
      setFormValue({
        nombre,
        email,
        role: rol,
        password: "",
      });
    }
  };
  const onSubmit = (e) => {
    e.preventDefault();
    const data = {
      nombre: values.nombre,
      email: values.email,
      rol: values.role,
      password: values.password,
    };
    console.log(data);
    if (id.length > 0) {
      socket.emit("editUser", { id, ...data });
    } else {
      socket.emit("addUser", data);
    }
    // reset();
    props.handleClose();
  };

  return (
    <Modal show={props.show} onHide={props.handleClose} animation={false}>
      <Modal.Header closeButton className="bg-light">
        {id.length > 0 ? <h5>Editar Usuario</h5> : <h5>Nuevo Usuario</h5>}
      </Modal.Header>
      <Modal.Body>
        <Form>
          <div className="mb-3 row">
            <label className="col-sm-2 col-form-label">Nombre</label>
            <div className="col-sm-10">
              <Form.Control
                id="area"
                name="nombre"
                value={values.nombre}
                onChange={handleInputChange}
                placeholder="Usuario"
              ></Form.Control>
            </div>
          </div>
          <div className="mb-3 row">
            <label className="col-sm-2 col-form-label">Email</label>
            <div className="col-sm-10">
              <Form.Control
                name="email"
                value={values.email}
                onChange={handleInputChange}
                placeholder="@email"
              ></Form.Control>
            </div>
          </div>
          <div className="mb-3 row">
            <label className="col-sm-2 col-form-label">Contrasea</label>
            <div className="col-sm-10">
              <Form.Control
                name="password"
                type="password"
                value={values.password}
                onChange={handleInputChange}
                placeholder="Contrasea"
              ></Form.Control>
            </div>
          </div>

          <div className="mb-3 row">
            <label className="col-sm-2 col-form-label">Rol</label>
            <div className="col-sm-10">
              <Form.Select
                name="role"
                value={values.role}
                onChange={handleInputChange}
              >
                <option>ADMIN_ROLE</option>
                <option>USER_ROLE</option>
              </Form.Select>
            </div>
          </div>
        </Form>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="secondary" onClick={props.handleClose}>
          Calcelar
        </Button>
        <Button variant="primary" onClick={onSubmit}>
          Guardar
        </Button>
      </Modal.Footer>
    </Modal>
  );
};
