import React, { useContext, useState } from "react";
import { MdAdminPanelSettings } from "react-icons/md";
import { FaPenSquare, FaRegTrashAlt, FaUserCircle } from "react-icons/fa";
import { MainContext } from "../context/MainContext";
import { AddUser } from "./AddUser";
import { OppUsers } from "./OppUsers";
import { ConfirmDelete } from "./ConfirmDelete";

export const ListUsers = () => {
  const { mainState } = useContext(MainContext);
  const { usuarios } = mainState;
  const [user, setUser] = useState({});
  const [show, setShow] = useState(false);
  const [smShow, setSmShow] = useState(false);
  const [dEdit, setdEdmit] = useState({});
  const editUser = (us) => {
    setUser(us);
    setShow(true);
  };
  const delShow = (item) => {
    setdEdmit({ id: item.id, nombre: item.nombre, type: "usuario" });
    setSmShow(true);
  };
  return (
    <div className="card m-3">
      <div className="card-header bg-info bold">Usuarios</div>
      <nav className="navbar navbar-light bg-light">
        <div className="container-fluid">
          <AddUser />
        </div>
      </nav>
      <div className="card-body shadow  bg-body rounded">
        <table className="table table-responsive  table-striped">
          <thead>
            <tr>
              <th scope="col">Nombre</th>
              <th scope="col">Email</th>
              <th scope="col">Rol</th>
              <th scope="col"></th>
            </tr>
          </thead>
          <tbody>
            {usuarios.map((usuario) => (
              <tr key={usuario.id}>
                <th>{usuario.nombre}</th>
                <td>{usuario.email}</td>
                <td>
                  {usuario.rol === "USER_ROL" ? (
                    <MdAdminPanelSettings className="icontab" />
                  ) : (
                    <FaUserCircle className="icontab" />
                  )}
                  {usuario.rol}
                </td>

                <td className="text-center">
                  <FaPenSquare
                    style={{
                      color: "#0298db",
                      fontSize: "23px",
                      marginRight: "10px",
                    }}
                    onClick={() => editUser(usuario)}
                  />
                  <FaRegTrashAlt
                    style={{ color: "#0298db", fontSize: "20px" }}
                    onClick={() => delShow(usuario)}
                  />
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
      <OppUsers
        show={show}
        handleClose={() => {
          setShow(false);
        }}
        usuario={user}
      />
      <ConfirmDelete
        show={smShow}
        handleClose={() => {
          setSmShow(false);
        }}
        item={dEdit}
      />
    </div>
  );
};
