import React, { useContext, useEffect, useState } from "react";
import { Button } from "react-bootstrap";
import { MainContext } from "../context/MainContext";
import { fetchConToken } from "../helpers/fetch";
import { types } from "../types/types";
export const Paginate = () => {
  const { dispatch } = useContext(MainContext);
  const [pag, setPag] = useState(15);
  const [total, setTotal] = useState(0);

  useEffect(() => {
    fetchAux(0);
  }, []);

  const Paginate = async (oper) => {
    if (pag >= 0) {
      if (oper === "+") {
        setPag(pag + 15);
      }
      if (oper === "-") {
        if (pag > 0) {
          setPag(pag - 15);
        }
      }

      fetchAux(pag);
    }
  };
  const fetchAux = async (pag) => {
    const resp = await fetchConToken(
      `roteos/?limite=15&desde=${pag}`,
      {},
      "GET"
    );
    if (resp.emisores) {
      dispatch({
        type: types.cargarEmisores,

        payload: resp.emisores,
      });
      const tt=parseInt(resp.total);
      setTotal(tt);
    }
  };


  return (
    <nav aria-label="Page navigation ">
      {total > pag && (
        <ul className="pagination">
          <Button className="page-item page-link" onClick={() => Paginate("-")}>
            Anterior
          </Button>

          <Button
            className="page-item page-link  "
            onClick={() => Paginate("+")}
          >
            Siguiente
          </Button>
        </ul>
      )}
    </nav>
  );
};
