import React, { useEffect } from "react";

import Chart from "react-apexcharts";
import { Form } from "react-bootstrap";

import { useForm } from "../../hooks/useForm";
import { useGraphic } from "../../hooks/useGraphic";

export const Ping = ({ id }) => {
  const [values, handleInputChange] = useForm({ ping: "30 min" });
  const { ping } = values;
  const { loading, data, fechas, setValues } = useGraphic({
    id,
    time: ping,
    endpoint: "graf/pings",
  });
  useEffect(() => {
    setValues({ id, time: ping });
  }, [ping]);

  if (loading) {
    return (
      <div className="col-12 card bg-main justify-content-center">
        <div className="text-center col-auto">
          <div className="spinner-border text-success">
            <span className="visually-hidden">Loading...</span>
          </div>
        </div>
      </div>
    );
  }
  const options = {
    chart: {
      id: "apexchart-example",
      zoom: {
        enabled: true,
      },
    },
    xaxis: {
      type: "category",
      categories: fechas,
      tickAmount: 12,
    },
    stroke: {
      curve: "smooth",
    },
    dataLabels: {
      enabled: false,
    },
    colors: ["#14ab13"],
  };
  const series = [
    {
      name: "ping",
      data,
    },
  ];
  return (
    <div className="col-12 card bg-main justify-content-center">
      <div className="col-auto">
        <div className="card-header bg-cardh" style={{ background: "#008000" }}>
          <div className="d-flex justify-content-between">
            <div className="mt-2 bold-text">Ping</div>
            <Form.Select
              style={{ width: "150px" }}
              value={ping}
              onChange={handleInputChange}
              name="ping"
            >
              <option>30 min</option>
              <option>Ultima hora</option>
              <option>Dia</option>
              <option>Semana</option>
              <option>Mes</option>
              <option>Ao</option>
            </Form.Select>
          </div>
        </div>
        <Chart
          options={options}
          series={series}
          type="area"
          width="100%"
          height={320}
        />
      </div>
    </div>
  );
};
