import React, { useEffect } from "react";
import { useContext } from "react";

import Chart from "react-apexcharts";
import { Form } from "react-bootstrap";
import { MainContext } from "../../context/MainContext";
import { SocketContext } from "../../context/SocketContext";
import { useForm } from "../../hooks/useForm";
import { useGraphic } from "../../hooks/useGraphic";

export const IntencitySignal = ({ id}) => {
  const { socket } = useContext(SocketContext);
  const { mainState } = useContext(MainContext);
  const { emisorActivo } = mainState;
  const { ip, marca,modo} = emisorActivo;
  const [values, handleInputChange] = useForm({ ping: "30 min" });
  const { ping } = values;
  const { loading, data, fechas, setValues } = useGraphic({
    id,
    ip,
    modo,
    time: ping,
    type:marca,
    endpoint: "graf/intencity",
  });
  useEffect(() => {
    setValues({ id, time: ping, type:marca ,ip,modo});
  }, [ping,marca,modo]);

  if (loading) {
    return (
      <div className="col-12 card bg-main justify-content-center">
        <div className="text-center col-auto">
          <div className="spinner-border text-success">
            <span className="visually-hidden">Loading...</span>
          </div>
        </div>
      </div>
    );
  }
  const options = {
    chart: {
      id: "apexchart-intencity",
      zoom: {
        enabled: true,
      },
    },
    xaxis: {
      type: "category",
      categories: fechas,
      tickAmount: 12,
    },
    stroke: {
      curve: "smooth",
    },
    dataLabels: {
      enabled: false,
    },
    colors: ['#ec340b'],
  };
  const series = [
    {
      name: "Intencidad",
      data,
    },
  ];
  return (
    <div className="col-12 card bg-main justify-content-center">
      <div className="col-auto">
        <div className="card-header bg-cardh" style={{ background:'#cc0000' }}>
          <div className="d-flex justify-content-between">
            <div className="mt-2 bold-text">Intencidad de seal</div>
            <Form.Select
              style={{ width: "150px" }}
              value={ping}
              onChange={handleInputChange}
              name="ping"
            >
              <option>30 min</option>
              <option>Ultima hora</option>
              <option>Dia</option>
              <option>Semana</option>
              <option>Mes</option>
              <option>Ao</option>
            </Form.Select>
          </div>
        </div>
        <Chart
          options={options}
          series={series}
          type="area"
          width="100%"
          height={320}
        />
      </div>
    </div>
  );
};
